          </div>
        </div>
        <!-- 热销 优惠券 充值 付款 -->
        <div class="recharge">
          <ul>
            <li @click="toKind">
              <img src="http://img.zhichiwangluo.com/zcimgdir/album/file_5b5571cdd0e48.png">
