            </li>
          </ul>
        </div>
        <!-- 通知栏 -->
        <van-notice-bar
          text="新鲜水果到！有礼进店享！即日起，在本平台消费满50元减5元，满100元减10元，满200元减50元！欢迎选购！"
          left-icon="volume-o"
        />
        <!-- 秒杀模块 -->
        <div class="discount">
          <div class="discount-banner">
            <img src="http://img.zhichiwangluo.com/zcimgdir/album/file_5b5578c260c84.png">
            <div class="sign">
