              <li>
                <div class="discount-img">
                  <img src="http://img.zhichiwangluo.com/zcimgdir/album/file_5b56958218e8a.png">
                </div>
                <div class="discount-info">
                  <div class="discount-title">心扉鲜果礼盒（进口苹果鲜橙龙眼）</div>
                  <div class="discount-price">
                    <span class="price1">￥99.00 </span> <span class="price2"> ￥119.00</span>
                  </div>
                  <div class="discount-time">距离活动结束
                    <van-count-down
                      :time="time"
                      format="DD 天 HH 时 mm 分 ss 秒"
                    />
                  </div>
                  <div class="discount-shop">
                    <div class="panic-buy">马上抢购</div>
                    <p>每人限购1份</p>
                  </div>
                </div>
              </li>
              <li>
                <div class="discount-img">
                  <img src="http://img.zhichiwangluo.com/zcimgdir/album/file_5b56958218e8a.png">
                </div>
                <div class="discount-info">
                  <div class="discount-title">心扉鲜果礼盒（进口苹果鲜橙龙眼）</div>
                  <div class="discount-price">
                    <span class="price1">￥99.00 </span> <span class="price2"> ￥119.00</span>
                  </div>
                  <div class="discount-time">距离活动结束
                    <van-count-down
                      :time="time"
                      format="DD 天 HH 时 mm 分 ss 秒"
                    />
                  </div>
