</template>
<script>
import Vue from 'vue'
import Prolist from '@/components/Prolist'
import Select from '@/components/Select'
import axios from 'axios'
import { Swipe, SwipeItem, Lazyload, NoticeBar, List, PullRefresh, CountDown, Search, Icon } from 'vant'

Vue.use(Swipe).use(SwipeItem)
Vue.use(Lazyload)
Vue.use(NoticeBar)
Vue.use(List)
Vue.use(PullRefresh)
Vue.use(CountDown)
Vue.use(Search)
Vue.use(Icon)
export default {
  data () {
    return {
      value: '',
      images: [
        'http://img.zhichiwangluo.com/zcimgdir/album/file_5b5550555f94b.png',
        'http://img.zhichiwangluo.com/zcimgdir/album/file_5b5550539b08d.png',
        'http://img.zhichiwangluo.com/zcimgdir/album/file_5b55505218e88.png'
      ],
      prolist: [],
      loading: false,
      finished: false,
      pageCode: 1,
      isLoading: false,
      time: 168 * 60 * 60 * 1000,
      goTop: true
    }
  },
  components: {
    Prolist,
    Select
  },
  beforeRouteLeave (to, from, next) {
    localStorage.setItem('postion', this.position)
    next()
  },
  created () {
    axios.get('/api/pro').then((res) => {
      this.prolist = res.data.data
    })
  },
  mounted () {
    window.addEventListener('scroll', this.handleScroll, true)
    let contentobj = document.getElementById('content')
    contentobj.addEventListener('scroll', () => {
      let position = contentobj.scrollTop
      // console.log(position)
      this.position = position
    })
  },
  // destroyed () {
  //   window.removeEventListener('scroll', this.scrollToTop)
  // },
  methods: {
    onSearch () {
      this.$router.push('/kind')
    },
    onCancel () {},
    toKind () {
      this.$router.push('/kind')
    },
    toCoupon () {
      this.$router.push('/coupon')
    },
    toRecharge () {
      this.$router.push('/recharge')
    },
    toPayment () {
      this.$router.push('/payment')
    },
    tocart () {
      this.$router.push('/cart')
    },
    handleScroll (e) {
      let scrolltop = e.target.scrollTop
      scrolltop > 30 ? (this.gotop = true) : (this.gotop = false)
    },
    backTop () {
      let top = document.getElementById('content').scrollTop
      const timeTop = setInterval(() => {
        document.getElementById('content').scrollTop = document.getElementById('content').scrollTop = top -= 50
        if (top <= 0) {
          clearInterval(timeTop)
        }
      }, 10)
      // document.getElementById('content').scrollTop = 0
    },
    onLoad () {
      console.log('上拉加载开始')
      this.loading = true
      axios.get('/api/pro', {
        params: {
          pageCode: this.pageCode,
          limitNum: 10
        }
      }).then(res => {
        this.loading = false
        this.pageCode++
        if (res.data.code === '10000') {
          this.finished = true
        } else {
          this.prolist = [...this.prolist, ...res.data.data]
        }
      })
    },
    onRefresh () {
      this.isLoading = true
      axios.get('/api/pro').then((res) => {
        this.isLoading = false
        this.prolist = res.data.data
        this.pageCode = 1
        this.finished = false
      })
    }
  },
  watch: {
    pageCode (newval, oldval) {
      if (newval > 1) {
        this.topflag = true
      } else {
        this.topflag = false
      }
    },
    $route (newval) {
      console.log(newval)
      if (newval.name === 'home') {
        let pst = localStorage.getItem('postion')
        // console.log(pst)
        document.getElementById('content').scrollTop = pst
      }
    }
  }
}
</script>
